import Vue from 'vue'
import App from './App.vue'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import './lib/mui/css/mui.css'
// 1.1 
import VueRouter from 'vue-router'

Vue.use(MintUI)

// 1.2 
Vue.use(VueRouter)


// 1.3 router.js
import router from './router.js'

// 2.1 vue-resource
import VueResource from 'vue-resource'
// 2.2 vue-resource
Vue.use(VueResource)

// 
Vue.http.options.root = 'http://120.77.181.41:3000'
//   post    { emulateJSON: true }
Vue.http.options.emulateJSON = true

// vuex
import Vuex from 'vuex'
Vue.use(Vuex)

import Vant from 'vant'
import 'vant/lib/index.css'
Vue.use(Vant)

// main.jsstore

var car = JSON.parse(localStorage.getItem('car') || '[]')

var store = new Vuex.Store({
  state: {  // this.$store.state.***
    // 
    // car
    // {id: id, name: ,  count: , price: , selected: , img: }
    car: car
  },
  mutations: { // this.$store.commit('' )
    addToCar(state, goodsinfo) {
      // storecar
      //  1.
      // 2. pushcar

      // flag
      var flag = false

      state.car.some(item => {
        if(item.id === goodsinfo.id) {
          item.count = parseInt(item.count) +  parseInt(goodsinfo.count)
          flag = true
          return true
        }
      })

      // 
      if(!flag) {
        state.car.push(goodsinfo)
      }

      // carcarlocalStorage
      localStorage.setItem('car', JSON.stringify(state.car))
    },
    updateGoodsInfo(state, goodsinfo) {
      // 
      // 
      state.car.some(item => {
        if(item.id == goodsinfo.id) {
          item.count = goodsinfo.count
          return true
        }
      })

      localStorage.setItem('car', JSON.stringify(state.car))
    },
    removeFromCar(state, id) {
      //  id store
      state.car.some( (item, i) => {
        if(item.id == id) {
          state.car.splice(i, 1)
          return true
        }
      })

      localStorage.setItem('car', JSON.stringify(state.car))
    },
    updateGoodsSelected(state, info) {
      state.car.some(item => {
        if(item.id == info.id) {
          item.selected = info.selected
        }
      })

      localStorage.setItem('car', JSON.stringify(state.car))
    }
  },
  getters: {  //  this.$store.getters.***
    getAllCount(state) {
      var c = 0
      state.car.forEach(item => {
        c += parseInt(item.count)
      })

      return c
    },
    getGoodsSelected(state) {
      var o = {}
      state.car.forEach( item=> {
        o[item.id] = item.selected
      } )

      return o
    },
    getGoodsCountAndAmount(state) {
      var o = {
        count: 0,  // 
        amount: 0  // 
      }

      state.car.forEach(item => {
        if(item.selected == true) {
          o.count += parseInt(item.count)
          o.amount += item.price * item.count
        }
      })

      return o
    }
  }
})

// 
import moment from 'moment'
// 
Vue.filter('dateFormat', function (dateStr, pattern='YYYY-MM-DD HH:mm:ss') {
  return moment(dateStr).format(pattern)
})

// 
import VuePreview from 'vue-preview'
Vue.use(VuePreview)


Vue.config.productionTip = false

// localStorage
window.addEventListener('beforeunload', ()=>{ localStorage.removeItem('car')})

new Vue({
  // 1.4 vm
  router,
  store,
  render: h => h(App),
}).$mount('#app')
