<template>
    <div class="goodsinfo-container">
        <transition
          @before-enter="beforeEnter"
          @enter="enter"
          @after-enter="afterEnter">
            <div class="ball" v-show="ballFlag" ref="ball"></div>
        </transition>

        <!--  -->
        <div class="mui-card">
            <div class="mui-card-content">
                <swiper :lunbutu-list="this.lunbutuList" :isfull="false"></swiper>
            </div>
        </div>

        <!--  -->
        <div class="mui-card goods-sell">
            <div class="mui-card-header">{{ this.goodsinfo.name }}</div>
            <hr>
            <!---->
            <div class="mui-card-content">
                <div class="mui-card-content-inner">
                    <p class="price">
                         <del>{{ this.goodsinfo.oldPrice }}</del> &nbsp;&nbsp;&nbsp;&nbsp; <span class="now_price">{{ this.goodsinfo.newPrice }}</span>
                    </p>
                    <p>
                        <numbox @getCount="getSelectedCount" :max="goodsinfo.quantity"></numbox>
                    </p>
                    <p>
                        <mt-button type="primary" size="small"></mt-button>
                        <mt-button type="danger" size="small" @click="addToShopCar"></mt-button>
                    </p>
                </div>
            </div>
        </div>

        <!--  -->
        <!-- 1. goodsinfonumber
             2. goodsinfo
             3.     
             4.  -->

        <!--  -->
        <div class="mui-card goods-params">
            <div class="mui-card-header"></div>
            <hr>
            <div class="mui-card-content">
                <div class="mui-card-content-inner">
                    <p>{{ this.goodsinfo.godNum }}</p>
                    <p>{{ this.goodsinfo.quantity }}</p>
                    <p>{{ this.goodsinfo.addDate | dateFormat }}</p>
                </div>
            </div>
            <div class="mui-card-footer">
                <mt-button type="primary" size="large" plain @click="goDesc(id)"></mt-button>
                <mt-button type="danger" size="large" plain @click="goComment(id)"></mt-button>
            </div>
        </div>
    </div>

</template>

<script>
    import { Toast } from 'mint-ui'
    import swiper from "../subcomponents/swiper";
    import goodsinfo_numbox from "../subcomponents/goodsinfo_numbox";
    export default {
        name: "GoodsInfo",
        data() {
            return {
                // iddata
                id: this.$route.params.id,
                // 
                lunbutuList: [],
                // 
                goodsinfo: [],
                // 
                ballFlag: false,
                // 1
                selectedCount: 1
            }
        },
        methods: {
            getGoodsInfo() {
                this.$http.get('api/getgoddetail?godId=' + this.id).then(result => {
                    if(result.body.status === 1) {
                        this.goodsinfo = result.body.god
                        this.lunbutuList = result.body.god.phos
                    } else {
                        Toast('')
                    }
                })
            },
            goDesc(id) {
                // 
                this.$router.push({name: 'goodsdesc', params: { id }})
            },
            goComment(id) {
                // 
                this.$router.push({name: 'goodscomment', params: { id }})
            },
            addToShopCar() {
                // 
                this.ballFlag = ! this.ballFlag
                // {id: id, name: ,  count: , price: , selected: , img: }
                var info = {
                    id: this.id,
                    name: this.goodsinfo.name,
                    count: this.selectedCount,
                    price: this.goodsinfo.newPrice,
                    selected: true,
                    img: this.goodsinfo.imgIntro[0]}

                // storemutations, 
                this.$store.commit('addToCar', info)
            },
            beforeEnter(el) {
                el.style.transform = "translate(0, 0)"
            },
            enter(el, done) {
                // enter

                // 
                // 1.  
                // 2. 
                // 3. 
                // 4. 
                // 5.  domObject.getBoundingClientRect()

                // 
                const ballPosition = this.$refs.ball.getBoundingClientRect()
                const badgePosition = document.getElementById('badge').getBoundingClientRect()
                const xDist = badgePosition.left - ballPosition.left
                const yDist = badgePosition.top - ballPosition.top

                // console.log(ballPosition)
                // console.log(badgePosition)

                setTimeout(() => {
                    el.style.transform = `translate(${xDist}px, ${yDist}px)`
                    el.style.transition = 'all 0.5s ease'
                    done()
                }, 20)
            },
            afterEnter(el) {
                this.ballFlag = ! this.ballFlag
                el.offsetWidth
            },
            getSelectedCount(count) {
                // data
                this.selectedCount = count
                // console.log(':' + count)
            }
        },
        created() {
            this.getGoodsInfo()
        },
        components: {
            swiper,
            numbox: goodsinfo_numbox
        }
    }
</script>

<style lang="scss" scoped>

    .goodsinfo-container{
        background-color: #eeeeee;
        overflow: hidden;
        color: black;

        .now_price{
            color: red;
            font-size: 16px;
            font-width: bold;
        }

        .mui-card.goods-sell{
            text-align: left;
            .mui-card-header{
                padding-left: 20px;
                padding-top: 20px;
            }

            .mui-card-content{
                padding-left: 20px;
            }

            .mui-numbox{
                height: 28px;
            }

            hr{
                background-color: #eeeeee;
                height: 1px;
                border: none;
            }

            .mint-button{
                margin-right: 10px;
            }

        }

        .mui-card.goods-params{
            text-align: left;
            .mui-card-header{
                padding-left: 20px;
                padding-top: 20px;
            }

            .mui-card-content{
                padding-left: 20px;
            }

            hr{
                background-color: #eeeeee;
                height: 1px;
                border: none;
            }
        }

        .mui-card-footer{
            display: block;
            button{
                margin: 15px 0;
            }
        }

        .ball{
            width: 15px;
            height: 15px;
            border-radius: 50%;
            background-color: red;
            position: absolute;
            z-index: 99;

            top: 341px;
            left: 156px;
        }
    }

</style>