<template>
    <div class="photoinfo-container">
        <h3>{{ photoinfo.type }}</h3>
        <p class="subtitle">
            <span> {{ Date.now() | dateFormat }}</span>
            <span>{{ 198 }}</span>
        </p>

        <hr>

        <!--  -->
        <div class="thumbs">
            <img class="preview-img" v-for="(item, index) in list" :key="item.src" :src="item.src"
                 height="100" @click="$preview.open(index, list)">
        </div>

        <!--  -->
        <div class="content" v-html="photoinfo.intro"></div>

        <!--  -->
        <comment :id="id" :get-comments-url="this.getcommentsurl" :post-comments-url="this.postcommentsurl"></comment>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui'
    import comment from "../subcomponents/comment";
    export default {
        name: "PhotoInfo",
        data() {
            return {
                id: this.$route.params.id,
                // 
                photoinfo: {},
                getcommentsurl: 'api/getphocom?phoId=',
                postcommentsurl: 'api/addphocom?phoid=',
                // 
                list: []
            }
        },
        methods: {
            getPhotoInfo() {
                // 
                this.$http.get('api/getphodetail?phoId=' + this.id).then(result => {
                    if(result.body.status === 1) {
                        // 
                        result.body.pho.phos.forEach( item => {
                            var temp = {}
                            temp.src = item
                            temp.w = 300
                            temp.h = 400

                            this.list.push(temp)
                        })

                    } else {
                        Toast('')
                    }
                })
            }
        },
        components: {
            comment
        },
        created() {
            this.getPhotoInfo()
        }
    }
</script>

<style lang="scss" scoped>

    .photoinfo-container{
        padding: 3px;

        h3{
            color: #26a2ff;
            font-size: 15px;
            margin: 15px 0;
        }

        .subtitle{
            display: flex;
            justify-content: space-between;
            font-size: 13px;
        }

        .content{
            color: black;
            font-size: 13px;
            line-height: 30px;
            text-align: left;
        }

        .thumbs{
            text-align: left;
            img{
                margin: 10px;
            }
        }
    }
</style>