<template>
    <div class="comment-container">
        <h3></h3>
        <hr>
        <textarea placeholder="(120)" maxlength="120" v-model="msg"></textarea>

        <mt-button type="primary" size="large" @click="postComment"></mt-button>

        <div class="cmt-list">
            <div class="cmt-item" v-for="(item, i) in commentList" :key="item.comId">
                <div class="cmt-title">
                    {{ i + 1}}&nbsp;&nbsp;{{ item.username }}&nbsp;&nbsp; {{ item.comDate | dateFormat }}
                </div>
                <div class="cmt-body">
                    {{ item.content ==='' ? '' : item.content }}
                </div>
            </div>
        </div>

        <mt-button type="danger" size="large" plain @click="getMore"></mt-button>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui'
    export default {
        name: "comment",
        data() {
            return {
                // 
                pageIndex: 1,
                commentList: [],
                // 
                msg: ''
            }
        },
        props: [
          'id', 'getCommentsUrl', 'postCommentsUrl'
        ],
        methods: {
            getComments(){
                // getCommentsUrl: 'api/getnewscom?newsid='
                this.$http.get(this.getCommentsUrl + this.id + '&pageIdx=' + this.pageIndex).then(result => {
                    if(result.body.status === 1) {
                        // this.commentList = result.body.comments
                        // 
                        this.commentList = this.commentList.concat(result.body.comments)
                    } else {
                        Toast('')
                    }
                })
            },
            // 
            getMore() {
                this.pageIndex++
                this.getComments()
            },
            postComment() {
                // 

                // 
                if(this.msg.trim().length === 0){
                    return Toast('')
                }


                // 1 URL
                // 2  { content: this.msg }
                // 3:   { emulateJSON: true }
                // 'api/addnewscom?newsId='
                this.$http.post(this.postCommentsUrl + this.id, {
                    content: this.msg.trim()
                }).then( result => {
                    if(result.body.status === 1) {
                        // 1. 
                        var cmt = { username: '',  comDate: Date.now(), content: this.msg.trim() }

                        this.commentList.unshift(cmt)
                        this.msg = ''
                    } else {
                        Toast('')
                    }

                })
            }
        },
        created() {
            this.getComments()
        }
    }
</script>

<style lang="scss" scoped>
    .comment-container{
        color: black;

        h3{
            font-size: 16px;
            text-align: left;
        }

        textarea{
            font-size: 14px;
            height: 85px;
            margin: 0;
        }

        .cmt-list{
            margin: 5px 0;
            .cmt-item{
                font-size: 13px;
                .cmt-title{
                    background-color: #cccccc;
                    text-align: left;
                    line-height: 30px;
                }

                .cmt-body{
                    line-height: 35px;
                    text-indent: 2em;
                    text-align: left;
                }
            }
        }
    }

</style>